.burger_wrapper {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  position: relative;
  margin-right: 1.5em;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  height: 2em;
}
@media (max-width: 680px) {
  .burger_wrapper span {
    display: none;
  }
}
.burger_wrapper .burger {
  display: inline-block;
  width: 1.5em;
  height: 2px;
  background-color: #F18700;
  position: relative;
  transition: .3s;
}
.burger_wrapper .burger:after,
.burger_wrapper .burger:before {
  content: '';
  display: inline-block;
  width: 1.5em;
  height: 2px;
  background-color: #F18700;
  position: absolute;
  left: 0;
  transition: .3s;
}
.burger_wrapper .burger:after {
  top: -0.5em;
}
.burger_wrapper .burger:before {
  bottom: -0.5em;
}
.burger_wrapper .burger.active {
  height: 0;
}
.burger_wrapper .burger.active:after {
  top: -0.2em;
  transform: rotate(45deg);
}
.burger_wrapper .burger.active:before {
  bottom: 0;
  transform: rotate(-45deg);
}
