.subscribe_block {
  margin-top: 3.2em;
  padding: 2.4em 0;
  position: relative;

  border-top: 3px solid @orange;

  p.subtext {
    font-size: .875em;
    text-align: center;

    @media (max-width: @screen-sm) {
      font-size: 1em;
    }
  }

  form {
    margin: 0 auto;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 32em;
    margin-top: 2em;

    @media (max-width: @screen-sm) {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-top: 1.5em;
    }

    .input_box {
      width: 20em;

      @media (max-width: @screen-sm) {
        width: 80%;
        margin: 0 auto;
        margin-bottom: 1em;
      }

      .input_text {
        font-size: 1em;
      }
    }
  }
}
